@import "basic";

/*---------------------------------contact.css--------------------------------------------*/

/*----------------------------Header------------------------------*/

@import "nav-bar";


.contact-us {
	background: #f4f4f4 url("https://picsum.photos/1600/1030/") 50% 50% no-repeat fixed;
	padding: 60px 0px 60px 0px;
	text-align: center;
	h1 {
		color: @title-dark;
		font-family: @Mr;
		font-size: 44px;
		font-weight: 700;
		text-transform: uppercase;
	}
	p {
		text-align: center;
		margin-top: 26px;
		text-transform: capitalize;
		font-size: 14px;
		a{
			margin-right: 7px;
			color:white;
		}
		i{
			color:white;
			margin-right: 7px;
		}
	}
}

/*----------------------------------Content-----------------------*/

.infos {
	margin-top: 75px;
	.container.small{
		max-width:1033px;
		.col-x:first-child{
			h2 {
				font-family:@SSP;
				color: @title-dark;
				font-weight: 700;
				text-transform: uppercase;
				font-size: 28px;
			}
			p{
				margin-top: 35px;
				color:#343434;
				font-size: 16px;
			}
			.info{
				margin-top: 80px;
				.col-xs-4 {
					.title{
						font-size: 16px;
						color: #303030;
						font-weight: 700;
					}
					p{
						word-wrap:break-word;
						margin-top: 20px;
						font-size: 14px;
					} 
				}
			}
		}
		.col-x:nth-child(2){
			form{
				float: right;
				.title{
					color:#343434;
					font-size: 16px;
					font-weight: bold;
				}

				.form-control{
					max-width: 352px;
					min-width: 320px;
					border-radius: 17px;
					border:1px solid #c0c0c0;
					color:#c0c0c0;
					height:40px; 
					padding: 6px 18px;
					&:focus{
						outline:none;
						border:1px solid @navi_active_color;
						box-shadow: inset 0 1px 1px rgba(0,0,0,.075), 
						0 0 8px rgba(214, 177, 97, 0.6);
					}
					&:first-child{
						margin-top: 21px;
					}
				}
				textarea.form-control{
					height:auto;
				}
				::placeholder{
					color:black;
				}
				[type="submit"]{
					width: 73px;
					min-width:0px;
					color: white;
					background-color:#383838;
					transition: all 0.3s ease;
					&:hover{
						background-color:@navi_active_color;
					}
				}
			}
		}
	}
}

.map{
	background-color: #f6f6f6;
	margin-top: 87px;
}



/*---------------------------------Footer--------------------------*/

#Footer {
	.misc {
		background-color: @background_color_6;
		.container {
			/*原本寫成padding:72px 0px 75px 0px;導致不小心把container內建
			的padding給覆蓋掉，然後就跑版了!fk~*/
			padding-top:82px;
		}
	}
	.col-left {
		.col-inner{
			>a{
				text-transform: capitalize;
			    cursor: pointer;
			    font-weight: 700;
			    font-size: 18px;
			    color: #575757;
			    &:hover{
			    	text-decoration: none;
			    }
			}
			ul.collapse.in{
				padding-left: 2px;
				padding-top:20px;
			}
			ul.collapsing{
				padding-left: 2px;
				padding-top:20px;
			}
			li a{
				color:@body_color;
			    line-height: 30px;
			    text-transform: capitalize;
			    font-weight: 700;
			    display: block;
			}
		}
	}
	.col-right {
		font-weight: 700;
		color: @color_9;
		a {
			text-transform: capitalize;
			color: @color_9;
		}
		div {
			i {
				padding-right: 3px;
			}
			i.fa-map-marker {
				display: inline-block;
				position: relative;
			}
		}
		#logo-gray {
			margin-top: 11px;
			width: 112px;
		}
		.contact_info {
			margin-top: 58px;
			line-height: 1.9rem;
			a {
				display: inline-block;
			}
		}
	}
	.copy-right {
		padding-top: 76px;
		padding-bottom: 117px;
		background-color: #f6f6f6;;	
		p {
			font-weight: 700;
			width: 245px;
			margin: 0 auto;
			margin-top: 5px;
			a {
				color: @color_12;
				font-family: @Mr;
				font-weight: 700;
				letter-spacing: 0px;
				transition: all 0.3s linear;
				&:hover {
					color: @color_13;
				}
			}
		}
		.pay-method{
			margin: 0 auto;
			width: 117px;
			img {
				position: relative;
				bottom: 3px;
			}
		}
	}
}

/*--------------------------------------------------------RWD-------------------------------------------------------*/


@media screen and (max-width:991px){
	#Content{
		.infos {
			.cleartest{
				clear:both;
			}
		
			.container.small{
				.col-x{
				}
				.col-x:nth-child(2){
					margin-top: 75px;
					form{
						max-width: 352px;
						margin: 0 auto;
						float: none;

						.form-control{
							min-width:0px;
						}
					}
				}
			}
		}
	}

	
	#Footer{
		.row{
			*{
				float:none!important;
			}
			.col-x{
				text-align: center;
			}
			>.col-right{
				float:none;
				#logo-gray{
					width: 112px;
					margin:0 auto;
				}
			}
			>.col-left{
				padding-bottom:30px;
				.col-inner{
					padding-bottom:20px;
				}
			}
		}
	}
}




